import React from "react";
import "./DefaultResetTemplate.css";

export interface DefaultResetTemplateProps {
    email: string;
    onEmailChange: (value: string) => void;
    onSubmit: (e: React.FormEvent) => void;
    loading: boolean;
    error: string | null;
    successMessage: string | null;
}

const DefaultResetTemplate: React.FC<DefaultResetTemplateProps> = ({
    email,
    onEmailChange,
    onSubmit,
    loading,
    error,
    successMessage,
}) => {
    return (
        <div className="reset-wrapper">
            <div className="reset-card">
                <div className="reset-header">
                    <div className="reset-icon-box">
                        <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round">
                            <path d="M21 2l-2 2m-7.61 7.61a5.5 5.5 0 1 1-7.78 7.78 5.5 5.5 0 0 1 7.78-7.78z" />
                            <path d="M12 21l2 2m5-5l2 2m2-5l-2-2" />
                        </svg>
                    </div>
                    <h1 className="reset-title">Reset your password</h1>
                    <p className="reset-subtitle">
                        Enter your email and we'll send you a reset link.
                    </p>
                </div>

                {successMessage && (
                    <div className="reset-success">
                        <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round">
                            <path d="M22 11.08V12a10 10 0 1 1-5.93-9.14" />
                            <polyline points="22 4 12 14.01 9 11.01" />
                        </svg>
                        <span>{successMessage}</span>
                    </div>
                )}

                {error && (
                    <div className="reset-error">
                        <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                            <circle cx="12" cy="12" r="10" />
                            <line x1="12" y1="8" x2="12" y2="12" />
                            <line x1="12" y1="16" x2="12.01" y2="16" />
                        </svg>
                        <span>{error}</span>
                    </div>
                )}

                <form onSubmit={onSubmit} className="reset-form">
                    <div className="form-group">
                        <label className="form-label">Email Address</label>
                        <input
                            type="email"
                            className="reset-input"
                            placeholder="your@email.com"
                            value={email}
                            onChange={(e) => onEmailChange(e.target.value)}
                            disabled={loading || !!successMessage}
                            required
                        />
                    </div>

                    <button
                        type="submit"
                        className="reset-button"
                        disabled={loading || !!successMessage}
                    >
                        {loading ? (
                            <div className="loading-dots">
                                <span></span><span></span><span></span>
                            </div>
                        ) : (
                            "Send reset link"
                        )}
                    </button>
                </form>

                <div className="back-to-login">
                    <a href="/login" className="login-link">
                        Back to Login
                    </a>
                </div>
            </div>
        </div>
    );
};

export default DefaultResetTemplate;
