import React, { useState } from "react";
import DefaultResetTemplate from "../components/templates/DefaultResetTemplate";

const ResetPasswordPage = () => {
    const [email, setEmail] = useState("");
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const [successMessage, setSuccessMessage] = useState<string | null>(null);

    const handleResetRequest = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);
        setError(null);
        setSuccessMessage(null);

        console.log("[Auth] Password reset requested for:", email);

        try {
            // Simulate API delay
            await new Promise((resolve) => setTimeout(resolve, 1500));

            // Logic Simulation:
            // For demo purposes, we'll fail if the email is "fail@example.com"
            if (email === "fail@example.com") {
                throw new Error("System is currently unable to process requests. Please try again later.");
            }

            setSuccessMessage("If this email exists in our system, you will receive a password reset link shortly.");
            console.log("[Auth] Reset link simulated as sent.");

        } catch (err: any) {
            setError(err.message || "An unexpected error occurred. Please verify your connection.");
        } finally {
            setLoading(false);
        }
    };

    return (
        <DefaultResetTemplate
            email={email}
            onEmailChange={setEmail}
            onSubmit={handleResetRequest}
            loading={loading}
            error={error}
            successMessage={successMessage}
        />
    );
};

export default ResetPasswordPage;
